	local('#{$material-icons-path}/MaterialIcons-Regular'),
	url('#{$material-icons-path}/MaterialIcons-Regular.woff2') format('woff2'),
	url('#{$material-icons-path}/MaterialIcons-Regular.woff') format('woff'),
	url('#{$material-icons-path}/MaterialIcons-Regular.ttf') format('truetype');
}

