/**
 * This file is based on style.css. The socicons-path variable was added so that
 * the file can be included as a partial within our main style sheet.
 * ----------------------------------------------------------------------------
 */

@charset "UTF-8";

$socicon-path: "." !default;

@font-face {
  font-family: "socicon";
  src:url("#{$socicon-path}/fonts/socicon.eot");
  src:url("#{$socicon-path}/fonts/socicon.eot?#iefix") format("embedded-opentype"),
    url("#{$socicon-path}/fonts/socicon.woff") format("woff"),
    url("#{$socicon-path}/fonts/socicon.ttf") format("truetype"),
    url("#{$socicon-path}/fonts/socicon.svg#socicon") format("svg");
  font-weight: normal;
  font-style: normal;
}

[data-icon]:before {
  font-family: "socicon" !important;
  content: attr(data-icon);
  font-style: normal !important;
  font-weight: normal !important;
  font-variant: normal !important;
  text-transform: none !important;
  speak: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

[class^="socicon-"]:before,
[class*=" socicon-"]:before {
  font-family: "socicon" !important;
  font-style: normal !important;
  font-weight: normal !important;
  font-variant: normal !important;
  text-transform: none !important;
  speak: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

